<template>
  <div id="app">
			<div class="box">
					<div class="hed">
						<h4>购物清单</h4>
					</div>
					<ul class="ul">
						<li class="li1"><input type="checkbox" v-model="xz" class="qx"><span>全选</span></li>
						<li class="li2">商品</li>
						<li class="li3">数量</li>
						<li class="li4">单价</li>
						<li class="li5">金额</li>
						<li class="li6">操作</li>
					</ul>
					<div class="fot">
						<ul v-for="(x,y) in data" class="ul1">
							<li class="li1"><input type="checkbox" class="cbx" v-model="x.gx" @change="cf"></li>
							<li class="li2"><img :src="'./src/'+x.img"><span class="span">{{x.js}}</span></li>
							<li class="li3"><span><button class="jian" @click="sub(y)">-</button>{{x.num}}<button class="jia" @click="sup(y)">+</button></span></li>
							<li class="li4 aa">¥{{x.price}}.00</li>
							<li class="li5 aa">¥{{x.price*x.num}}.00</li>
							<li class="li6"><button class="rem" @click="remove(y)">删除</button></li>
						</ul>
					</div>
					<div class="foot">
						<span class="remall" @click="remall">删除所选商品</span><span class="jugw">继续购物</span>
						<div class="rig">
							<span class="zs">{{zs}}</span>件商品总计(不含运费)：<span class="zj">¥{{zj}}.00</span>
							<span class="qjs">去结算</span>
						</div>
					</div>
			</div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
    	 xz:false,
       data:[{
       	img:'assets/img/1.jpg',
       	js:'茶具',
       	price:40,
				num:1,
				gx:false
       },
       {
       	img:'assets/img/2.jpg',
       	js:'绿茶',
       	price:3,
				num:1,
				gx:false
       },
       {
       	img:'assets/img/3.jpg',
       	js:'奶粉',
       	price:80,
				num:1,
				gx:false
       },
       {
       	img:'assets/img/4.jpg',
       	js:'火腿肠',
       	price:1,
				num:1,
				gx:false
       },
       {
       	img:'assets/img/6.jpg',
       	js:'坚果',
       	price:20,
				num:1,
				gx:false
       }]
    }
  },
  methods:{
			sub(i){
				this.data[i].num--;
				this.data[i].num=Math.max(this.data[i].num,1);
				if (this.data[i].num==0) {
					this.data[i].gx=false;
				}
//				if (this.zs==0) {
//					this.xz=false;
//				}
			},
			sup(i){
				this.data[i].num++;
				this.data[i].num=Math.min(this.data[i].num,10);
				if (this.data[i].num==0) {
					this.data[i].gx=false;
				}
			},
			cf(){
				if (this.zs==0) {
					this.xz=false;
				}
			},
			remove(i){
				this.data.splice(i,1);
			},
			remall(){
				for (var i = 0; i < this.data.length; i++) {
					if (this.data[i].gx==true) {
					  this.data.splice(i,1);
					  i--;
					}
				}
				if (this.data.length==0) {
					 this.xz=false;
				}
			},
	},
	watch:{
			xz(val){
				for (var i = 0; i < this.data.length; i++) {
					this.data[i].gx=val;
					if (this.data[i].num==0) {
						this.data[i].gx=false;
					}
				};
			}
	},
	computed:{
			zj(){
				var sum=0;
				for (var i = 0; i < this.data.length; i++) {
					if(this.data[i].gx){
						sum+=this.data[i].price*this.data[i].num;
					}
				};
				return sum;
			},
			zs(){
				var sum1=0;
				for (var i = 0; i < this.data.length; i++) {
					if(this.data[i].gx){
						sum1+=this.data[i].num;
					}
				};
				return sum1;
			}
		},
}
</script>

<style>
*{
	padding: 0;
	margin: 0;
	list-style: none;
	user-select: none;
}
.box{
	width: 800px;
	border: 1px solid #e5e6e6;
	border-top: 2px solid #88a6ff;
	margin: 0 auto;
	margin-top: 20px;
}

.hed{
	height: 44px;
	width: 100%;
	border-bottom: 1px solid #e5e6e6;
}
h4{
	padding-left: 20px;
	line-height: 44px;
	font-size: 14px;
	color: #3b71ff;
}
.ul{
	width: 100%;
	height: 25px;
	font-size: 12px;
	border-bottom: 1px solid #e5e6e6;
}
.ul1{
	width: 100%;
	height:90px;
	box-sizing: border-box;
	padding-top: 10px;
}
.ul li{float: left;text-align: center;line-height: 25px;}
.ul1 li{float: left;text-align: center;}
.li1{width: 80px;}
.li2{width: 300px;}
.li3{width: 100px;}
.li4{width: 100px;}
.li5{width: 100px;}
.li6{width: 120px;}
.qx{margin-top: 6px;float: left;margin-right: 3px;margin-left:20px;}
.li1 span{float: left;}
.fot{width: 100%;}
.li2 img{
	width: 70px;
	height: 70px;
	border: 1px solid #e5e6e6;
	margin-right: 10px;
	float: left;
}
.cbx{
	float: left;
	margin-top: 30px;
	margin-left: 20px;
}
.span{
	float: left;
	font-size: 14px;
	
	line-height: 70px;
}
.li3 span{
	width: 60px;
	height: 25px;
	line-height: 25px;
	display: inline-block;
	border:1px solid #e5e6e6;
	font-size: 14px;
	margin-top: 20px;
}
.li3 span button{
	width: 15px;
	font-size: 16px;
	background: none;
	border: none;
	outline: none;
	box-sizing:border-box;
	cursor: pointer;
}
.jian{
	float: left;
	padding-top: 3px;
	padding-left: 2px;
}
.jia{
	float: right;
	padding-top: 4px;
	padding-right: 2px;
}
.aa{
	color: #ff5050;
	font-size: 14px;
	line-height: 68px;
}
.rem{
	background: none;
	border: none;
	outline: none;
	font-size: 14px;
	cursor: pointer;
	line-height: 65px;
}
.foot{
	width: 100%;
	height: 50px;
	font-size: 13px;
	box-sizing: border-box;
	padding-top: 20px;
}
.remall{
	float: left;
	display: block;
	margin-left: 25px;
	margin-right: 20px;
	font-size: 12px;
	cursor: pointer;
}
.jugw{
	font-size: 12px;
	float: left;
	margin-left: 5px;
	cursor: pointer;
}
.rig{
	float: right;
}
.zs,.zj{
	color: #ff5867;
}
.zj{
	font-size:16px;
}
.qjs{
	width: 70px;
	height: 30px;
	background-color: #fe6500;
	display: inline-block;
	color: #fff;
	text-align: center;
	line-height: 30px;
	font-size: 14px;
	margin-left:15px;
	cursor: pointer;
}
</style>



